<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">预约详情</block>
		</cu-custom>
		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange"></text> 座位信息
			</view>
		</view>
		<view style="padding-left: 3%;padding-right: 3%;">
			<view class="seattitle" style="margin-top: 5px;font-weight: bold">
				{{obj.floor}}({{obj.seat}})
			</view>
			<view class="seatdetail">
				{{obj.yuyuedatetime}}
			</view>
		</view>
		
		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange"></text> 相关操作
			</view>
		</view>
		<view class="butgroup margin-top">
			<button class="cu-btn bg-green margin-tb-sm lg" @tap="qiandao">预约签到</button>
			<button class="cu-btn bg-red margin-tb-sm lg" @tap="cancel">取消预约</button>
		</view>
		
		<view class="butgroup margin-top">
			<button class="cu-btn bg-blue margin-tb-sm lg" @tap="baoliu">座位保留</button>
			<button class="cu-btn bg-orange margin-tb-sm lg" @tap="xuyong">座位续用</button>
		</view>
		
		<view class="butgroup margin-top">
			<button class="cu-btn bg-red margin-tb-sm lg" style="width: 90%;"  @tap="jieshu">提前结束</button>
			<!-- <button class="cu-btn bg-pink margin-tb-sm lg"  @tap="shoucang">收藏座位</button> -->
		</view>
		

	</view>


</template>

<script>
	export default {
		onLoad(options) {
			this.obj = JSON.parse(options.val) 
			this.userinfoObj = uni.getStorageSync('userinfo')
		},
		mounted() {

		},
		data() {
			return {
				userinfoObj:{},
				obj:{},
				seatlist:[]
			};
		},
		methods: {
			async submitfun(val){
				let msgobj = this.obj
				msgobj.status = val
				msgobj.datetime = this.getnowdate() + " " + this.getnowtime()
				let resdata = await new this.Request(this.Urls.m().updatayuyuestatus, msgobj).modepost()
				if(resdata == 'success'){
					uni.showToast({
						title: val,
						icon: 'success',
						//显示持续时间为 2秒
						duration: 2000
					})  
				}
			},
			
			qiandao(){
				let that = this
				let currenttime = this.getnowdate() + " " + this.getnowtime()
				let yuyuetime = this.obj.startdate + " " + this.obj.starttime
				let currenttimenum = new Date(currenttime).getTime() 
				let yuyuetimenum = new Date(yuyuetime).getTime() 
				console.log(currenttimenum,yuyuetimenum)
				if(((yuyuetimenum * 1 - currenttimenum* 1)>0) && ((yuyuetimenum * 1 - currenttimenum* 1 )<5*60*1000)){
					uni.showModal({
						title: '提示',
						content: `是否进行预约签到`,
						showCancel: true,
						success: (res) => {
							if (res.confirm) {
								let msgobj = that.obj
								msgobj.status = "预约签到"
								msgobj.datetime = this.getnowdate() + " " + this.getnowtime()
								let msgstr = JSON.stringify(msgobj)
								uni.navigateTo({
									url: `/pages/my/qiandao?msgstr=${msgstr}`,
								});
								// that.submitfun("预约签到")
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}else{
					//当前时间不能预约打卡
					uni.showToast({
						title: '当前时间不能预约打卡',
						icon:"none",
						duration: 2000
					});
				}
				
			},
			cancel(){
				let that = this
				uni.showModal({
					title: '提示',
					content: `是否进行取消预约`,
					showCancel: true,
					success: (res) => {
						if (res.confirm) {
							that.submitfun("取消预约")
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			baoliu(){
				let that = this
				uni.showModal({
					title: '提示',
					content: `是否进行座位保留`,
					showCancel: true,
					success: (res) => {
						if (res.confirm) {
							that.submitfun("座位保留")
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			xuyong(){
				let that = this
				uni.showModal({
					title: '提示',
					content: `是否进行续用`,
					showCancel: true,
					success: (res) => {
						if (res.confirm) {
							that.submitfun("座位续用")
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			jieshu(){
				let that = this
				uni.showModal({
					title: '提示',
					content: `是否进行结束使用`,
					showCancel: true,
					success: (res) => {
						if (res.confirm) {
							that.submitfun("结束使用")
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			// qiandao(){
			// 	let that = this
			// 	uni.showModal({
			// 		title: '提示',
			// 		content: `是否进行预约签到`,
			// 		showCancel: true,
			// 		success: (res) => {
			// 			if (res.confirm) {
			// 				that.submitfun("预约签到")
			// 			} else if (res.cancel) {
			// 				console.log('用户点击取消');
			// 			}
			// 		}
			// 	});
			// },
			
			getnowdate() {
				var nowDate = new Date();
				var year = nowDate.getFullYear();
				var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1) : nowDate.getMonth() + 1;
				var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
				var dateStr = year + "-" + month + "-" + day;
				return dateStr;
			},
			getnowtime() {
				let date = new Date()
				let hours = date.getHours()
				let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
				let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
				let time = hours + ':' + minutes + ':' + seconds
				return time
			},
		}
	}
</script>

<style scoped>
.butgroup{
	padding-left: 5%;
	padding-right: 5%;
	width: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.cu-btn{
	width: 43%;
}
</style>